import { Component } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";

@Component({
  selector: "app-scan-failed",
  templateUrl: "./scan-failed.page.html",
  styleUrls: ["./scan-failed.page.scss"],
})
export class ScanFailedPage {
  public readonly ean$: Observable<string>;
  public constructor(activatedRoute: ActivatedRoute) {
    this.ean$ = activatedRoute.paramMap.pipe(map((p) => p.get("ean")));
  }
}
